/* src/components/NavigationHeader.css */

.header {
  background-color: #3498db; /* A modern and strong blue color */
  color: white;
  padding: 10px;
}

.navbar {
  display: flex;
  justify-content: space-between; /* Add space between logo and links */
  align-items: center;
}

.logo {
  font-size: 20px;
  color: #fff;
  text-decoration: none;
}

.nav-links {
  display: flex;
  gap: 20px;
}

.nav-link {
  color: #fff;
  text-decoration: none;
  font-size: 16px;
  padding: 10px 15px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

/* Style for the selected/active link */
.nav-link.active {
  background-color: #2c3e50; /* A slightly darker color when selected */
}
